<template>
  <div class="goodsList-container">
    <div style="height: 10px;"></div>

    <!-- 头部导航栏 -->
    <van-nav-bar
      fixed
      placeholder
      title="商品列表"
      left-text="返回"
      left-arrow
      @click-left="$router.back()"
    />

    <!-- /头部导航栏 -->
    <van-list
      v-model="loading"
      :immediate-check="false"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :offset="10"
    >
      <!-- 两列 -->
      <!-- <van-grid direction="horizontal" :column-num="2" :gutter="10">
        <van-grid-item
          v-for="item in goodslist"
          :key="item.goods_id"
          :to="/goodsinfo/ + item.goods_id"
        >
          <van-image
            lazy-load
            :src="item.goods_small_logo"
            width="100"
            height="100"
          >
            <span class="title">{{ item.goods_name }}</span>

            <h1>￥{{ item.goods_price }}</h1>
          </van-image>
        </van-grid-item>
      </van-grid> -->
      <!-- 列表 -->

      <van-cell
        v-for="item in goodslist"
        :key="item.goods_id"
        :to="/goodsinfo/ + item.goods_id"
      >
        <van-image
          width="150"
          height="150"
          lazy-load
          :src="item.goods_small_logo"
        >
          <span>{{ item.goods_name }}</span>
          <h1>￥{{ item.goods_price }}</h1>
        </van-image>
      </van-cell>
    </van-list>
    <!-- /商品列表顶部 -->

    <!-- 没数据时显示 -->
    <div class="no-data" v-if="!this.goodslist">
      <img src="../../assets/img/logo.png" alt="暂无记录" class="img" />
    </div>
  </div>
</template>

<script>
import { getGoodsList } from '@/api/api'

export default {
  name: 'goodsList',
  props: {},
  data() {
    return {
      goodslist: [],
      pagenum: 300, // 请求第几页
      pagesize: 20, // 每页请求的数量
      total: 0, // 总共的数据条数
      // list
      loading: false,
      finished: false
    }
  },

  created() {
    // 设置<van-list>组件 :immediate-check="false"
    // 理由：禁止 List 初始化时触发 load 事件。把加载第一屏的事件放在 created() 里面。
    this.getGoodsList()
  },
  methods: {
    getGoodsList() {
      const params = {
        pagenum: this.pagenum,
        pagesize: this.pagesize
      }

      getGoodsList(params).then(res => {
        const rows = res.message.goods // 请求返回当页的列表
        // console.log(rows)

        this.loading = false
        this.total = res.message.total

        if (rows == null || rows.length === 0) {
          // 加载结束
          this.finished = true
          return
        }
        // 将新数据与老数据进行合并
        this.goodslist.push(...rows) // 这里不能是等于，要不然就覆盖了
        // this.goodslist=this.goodslist.push(...rows) //错误的=>push() 方法可向数组的末尾添加一个或多个元素，并返回新的长度
        // this.goodslist = this.goodslist.concat(rows)
        // console.log(this.goodslist)
        // 如果列表数据条数>=总条数，不再触发滚动加载
        if (this.goodslist.length >= this.total) {
          this.finished = true
        }
      })
    },

    // 滚动加载时触发，list组件定义的方法
    onLoad() {
      console.log(this.pagenum)
      this.pagenum++
      this.getGoodsList()
    }
  }
}
</script>

<style scoped lang="less"></style>
